import {Component, ChangeDetectionStrategy, Input, Output, EventEmitter} from '@angular/core'
import { Todo } from '../reducers/todos.reducer';

@Component({
    selector:'app-todos',
    templateUrl:'todos.component.html',
    styleUrls:['todos.component.css'],
    changeDetection:ChangeDetectionStrategy.OnPush
})

// Presentation Component
export class TodoComponent{
    
    @Input()
    todos:Todo[];
    @Output()
    deleteevent:EventEmitter<string>  = new EventEmitter<string>();
    @Output()
    toggleevent:EventEmitter<string>  = new EventEmitter<string>();

    constructor(){
        
    }

    deleteTodo(i: any){
       this.deleteevent.emit(i);
    }
  
    public toggleComplete(i: any){
       this.toggleevent.emit(i);
    }
}


